body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

@font-face{
  font-family: "Playfair Display";
  src: url(/static/media/PlayfairDisplay-SemiBold.b172fff6.woff2) format('woff2');
  font-weight:600;
  font-style:normal;
  color: #eee;
}

@font-face{
  font-family: "Roboto";
  src: url(/static/media/Roboto-Regular.6887b6f2.woff2) format('woff2');
  font-weight:400;
  font-style:normal;
  color: #eee;
}

html {
  scroll-behavior: smooth;
}
body {
  background: #041924;
  background-attachment:fixed;
  color: #F9FEFF;
}
.container{
  padding-left: 8%;
  display: grid;
  grid-template-columns: 600px -webkit-min-content 1fr;
  grid-template-columns: 600px min-content 1fr;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}
.aboutBody{
  font-size: 1.5rem;
  width: 600px;
  align-items: center;
}

.subTitle{
  font-size: 3rem;
}

.intro{
  font-family: "Playfair Display";
  font-weight: 400;
  font-size: 4rem;
  padding-top: 20%;
  margin-bottom: 25%;
  color:#F9FEFF;
}

.left{
  padding-left: 8%;
}

.name{
  color: #75f0c3;
;
}

.positions{
  font-size: 3rem;
}

footer{
  text-align: center;
}

.accordion-section{
    display: flex;
    flex-direction: column;
    margin: 50px 100px;
}
.accordion{
    background-color: #F9FEFF;
    color: #041924;
    cursor: pointer;
    padding: 18px;
    display: flex;
    /* border-top-left-radius:15px;
    border-top-right-radius: 15px; */
    border-radius: 15px;
    flex-direction: column;
    align-items: center;
    border: none;
    outline: none;
    transition: background-color 0.6s ease;
}

.image{
    max-width: 100%;
    height: auto;
    border-radius:15px;
}

.accordion:hover{
    background-color: #ccc;
}
.active{
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
}
.accordion-club{
    font-family: 'Roboto', sans-serif;
    font-size: 2.5rem;
    text-align: center;
    padding-top: 10px;
}
.accordion-title{
    font-family: 'Roboto', sans-serif;
    font-size: 2rem;
    text-align: center;
    color: grey;
    margin-top: 10px;
    width: 100%;
}
.title:hover{
    border-bottom: 3px solid #75f0c3;
}

.accordion-icon{
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
    float: right;
    width: 40px;
    height: 40px;
    margin-top:-5px;
}

.rotate {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.accordion-content{
    background-color: #F9FEFF;
    color: #041924;
    overflow: hidden;
    transition: max-height 0.6s ease;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
}

.accordion-text{
    font-size: 1.5rem;
    padding:20px;
    line-height: 1.5;
}
